.social {
  @include vp-767 {
    flex-direction: row-reverse;
    gap: 0;
    padding-top: 10px;
    padding-left: 30px;
    justify-content: flex-end;
  }
  list-style: none;
  display: flex;
  gap: 15px;
  margin: 0;
  padding: 0;
  margin-top: 30px;
}

.social__link-img {
  @include vp-767 {
    width: 30px;
    height: 30px;
  }
  fill: $color-default-white;
}

.social__link {
  @include vp-767 {
    display: block;
    margin: 0;
    padding: 10px;
  }
}
